﻿import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AccountComponent } from './account.component';
import { LoginComponent } from './login/login.component';
import { UnauthorizedComponent } from './unauthorized/unauthorized.component';
import { SigninOidcComponent } from './login/signin-oidc/signin-oidc.component';
import { SignoutCallbackOidcComponent } from './login/signout-callback-oidc/signout-callback-oidc.component';


@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: AccountComponent,
                children: [
                    { path: 'login', component: LoginComponent },
                    { path: 'unauthorized', component: UnauthorizedComponent },
                    { path: 'signin-oidc', component: SigninOidcComponent },
                    { path: 'signout-callback-oidc', component: SignoutCallbackOidcComponent }
                ]
            }
        ])
    ],
    exports: [
        RouterModule
    ]
})
export class AccountRoutingModule { }
